import { stringify } from 'query-string';
import { makeApiRequest } from '../core';
import {
  DealClickResponse,
  DealSummary,
  DealSummaryWithDomains,
} from '../types';
import SimpleCache from '../../common/simpleCache';
import { endpoints } from '../../common/endpoints';
import { getSdkUser } from '../config';

const dealsCache = new SimpleCache<DealSummaryWithDomains | null>(
  'DOMAIN_LOOKUP',
  86_400 //24hrs
);

const interactionCache = new SimpleCache<Interaction>(
  'DEAL_INTERACTION',
  82_800 /* 24 hours */
);

export interface Interaction {
  type:
    | 'dismiss'
    | 'activate'
    | 'automatic'
    | 'webapp'
    | 'popup'
    | 'link'
    | 'error';
}

export const lookupByDomain = async (
  domain: string
): Promise<DealSummaryWithDomains | null> => {
  if (!domain) return null;

  const cachedResponse = await dealsCache.get(domain);

  if (cachedResponse !== undefined) {
    return cachedResponse as DealSummaryWithDomains;
  }
  const { userLocation } = await getSdkUser();

  const requestParams = {
    domain,
    platform: 'Mobile',
    affiliate: null,
    affiliateRule: null,
    location: userLocation === 'GB' ? 'UK' : userLocation, // we use UK not GB
  };

  const deal = await makeApiRequest<DealSummaryWithDomains>(
    `${endpoints.dealSearch}?${stringify(requestParams, {
      skipNull: true,
    })}`
  );

  await dealsCache.set(domain, deal);

  return deal;
};

export const getTrackingUrl = async (
  deal: DealSummary,
  analytics: Record<string, string | number>,
  url?: string
) => {
  const response = await makeApiRequest<DealClickResponse>(
    endpoints.dealActivation,
    {
      method: 'POST',
      body: JSON.stringify({
        campaignId: deal.id,
        storeUrl: url,
        analytics,
      }),
    }
  );

  return response?.link ?? null;
};

export const getInteractionByDomain = async (domain: string | null) => {
  let interaction: Interaction | null = null;

  if (domain !== null) {
    interaction = (await interactionCache.get(domain)) ?? null;
  }

  return interaction;
};

export const clearInteraction = async (domain: string) => {
  await interactionCache.delete(domain);
};

export const clearInteractionCache = () => {
  interactionCache.clear();
};

export const setInteraction = async (
  domain: string | null,
  interaction: Interaction
) => {
  if (domain !== null) {
    await interactionCache.set(domain, { type: interaction.type });
  }
};

export const setInteractions = async (
  domains: string[],
  interaction: Interaction
) => {
  if (domains.length > 0) {
    await interactionCache.appendWithSameValue(domains, interaction);
  }
};
