const testOpenTabBackground = () => {
  browser.tabs
    .create({
      active: false,
      url: 'https://example.com',
      pinned: false,
    })
    .then((tab) => {
      setTimeout(() => {
        if (tab.id) browser.tabs.remove(tab.id);
      }, 2000);
    });
};

const testNativeMessage = () => {
  browser.runtime
    .sendNativeMessage('application.id', {
      message: 'Hello from background page',
    })
    .then((response) => {
      console.log('Received sendNativeMessage response:');
      console.log(response);
    });
};
