const AUTOMATIC_ACTIVATION = 'preferences/AUTOMATIC_ACTIVATION';

export const hasAutomaticActivation = async (): Promise<boolean> => {
  const value = await getFromStorage<boolean>(AUTOMATIC_ACTIVATION);
  return value === true;
};

export const setAutomaticActivation = async (value: boolean): Promise<void> => {
  await setStorageValue(AUTOMATIC_ACTIVATION, value);
};

const getFromStorage = async <T>(key: string): Promise<T | undefined> => {
  return new Promise((resolve) => {
    browser.storage.local.get([key], (records: Record<string, unknown>) => {
      if (key in records) {
        resolve(records[key] as T);
      } else {
        resolve(undefined);
      }
    });
  });
};

const setStorageValue = async (key: string, value: any): Promise<void> => {
  await browser.storage.local.set({ [key]: value });
};
