import { getOtl } from '../api/core';
import { getDomainFromUrl } from '../common/helpers';
import env from '../env';

let handleActivationPromise: Promise<void> | null = null;

export const initActivationProcessor = () => {
  browser.tabs.getCurrent().then(async (tab) => {
    handleTabActivation(tab);
  });

  handleTabUpdates();
};

const handleTabUpdates = () => {
  browser.tabs.onUpdated.addListener(async (_, _changes, tab) => {
    handleTabActivation(tab);
  });
};

const handleTabActivation = async (tab: browser.tabs.Tab) => {
  if (handleActivationPromise === null) {
    handleActivationPromise = handleActivation(tab);
  }

  await handleActivationPromise;
  handleActivationPromise = null;
};

const activationLinks = [
  '/o2/activate',
  '/plugin-activation',
  '/o2/plugin-activation',
];

const handleActivation = async (tab: browser.tabs.Tab) => {
  if (
    tab &&
    tab.id &&
    tab.url &&
    getDomainFromUrl(tab.url)?.endsWith('kindred.co') &&
    activationLinks.includes(new URL(tab.url).pathname)
  ) {
    getOtl().then((otlId) => {
      if (otlId) {
        const otl = `${env.API_URL}/auth/partners/otl?otlId=${otlId}`;
        const url = new URL(otl);

        if (env.REACT_APP_IS_O2) {
          url.searchParams.set('loginType', '1');
        }

        browser.tabs.update(tab.id, {
          url: url.toString(),
        });
      }
    });
  }
};
