import { endpoints } from '../../common/endpoints';
import { makeApiRequest } from '../core';
import { Balance } from '../types';

interface BalanceUpdate {
  balance: Balance;
  updatedOn: number;
}

const TEN_MINUTES = 10 * 60 * 1000;
const BALANCE_KEY = 'balance';

export const hasBalancedChanged = async (): Promise<boolean> => {
  const latestBalanceCache: Record<string, unknown> =
    await browser.storage.local.get([BALANCE_KEY]);

  const now = new Date().getTime();
  let cachedBalance: Balance | undefined;

  if (latestBalanceCache) {
    const latestBalance = latestBalanceCache[BALANCE_KEY] as BalanceUpdate;
    if (latestBalance) {
      cachedBalance = latestBalance.balance;
      if (now - latestBalance.updatedOn < TEN_MINUTES) {
        return false;
      }
    }
  }

  const apiBalance = await makeApiRequest<Balance>(endpoints.balance);

  if (!apiBalance) {
    return false;
  }

  const hasChanged =
    cachedBalance !== undefined &&
    (cachedBalance.allTimeEarnings < apiBalance.allTimeEarnings ||
      cachedBalance.pending < apiBalance.pending);

  await browser.storage.local.set({
    [BALANCE_KEY]: {
      balance: apiBalance,
      updatedOn: now,
    },
  });

  return hasChanged;
};
