import { KindredPlatform } from '@kindredteam/api';
import jwtDecode from 'jwt-decode';
import CryptoJS from 'crypto-js';
import env from '../env';
import { getSdkAuth, getSdkUser, getUserTokens, setUserTokens } from './config';

export type HttpMethod = 'GET' | 'POST' | 'OPTION' | 'PUT' | 'DELETE';
export type RequestBody =
  | string
  | Blob
  | FormData
  | null
  | undefined
  | URLSearchParams;

interface RequestOptions {
  body?: RequestBody;
  method?: HttpMethod;
}
interface AuthResponse {
  accessToken: string;
  refreshToken: string;
}

let accessTokenRefreshPromise: Promise<string | null> | null = null;

let sdkAuthPromise: Promise<AuthResponse | null> | null = null;
let sdkAuthAttempt = 0;
const SDK_AUTH_RETRY_LIMIT = 5;

const EXPIRES_IN_MILISECONDS = 5_000; // 5 seconds

export async function makeApiRequest<T>(
  url: string,
  { body, method = 'GET' }: RequestOptions = {}
) {
  let requestUrl = url;
  if (!requestUrl.startsWith('https://') && !requestUrl.startsWith('http://')) {
    if (!requestUrl.startsWith('/')) {
      requestUrl = `/${requestUrl}`;
    }
    requestUrl = env.API_URL + requestUrl;
  }

  const headers = new Headers();

  let { accessToken, refreshToken } = await getUserTokens();

  if (
    env.REACT_APP_SDK_MODE &&
    (!accessToken || accessToken === '') &&
    sdkAuthAttempt < SDK_AUTH_RETRY_LIMIT
  ) {
    if (sdkAuthPromise === null) {
      sdkAuthAttempt++;
      sdkAuthPromise = sdkAuth();
    }
    const authResponse = await sdkAuthPromise;
    sdkAuthPromise = null;

    if (authResponse) {
      accessToken = authResponse.accessToken;
      refreshToken = authResponse.refreshToken;

      sdkAuthAttempt = 0;
    }
  }

  if (accessToken) {
    headers.append('Authorization', `Bearer ${accessToken}`);
  }

  if (typeof body === 'string') {
    headers.append('Content-Type', 'application/json');
  }

  // TODO need to make sure we create this new platform
  headers.append(
    'kindred-platform',
    `${KindredPlatform[KindredPlatform.Extension]}`
  );

  const retryLimit = 3;
  let retryCount = 0;

  while (retryCount < retryLimit) {
    try {
      if (accessToken && refreshAccessToken) {
        const { exp } = jwtDecode<{ exp: number }>(accessToken);

        if (Date.now() + EXPIRES_IN_MILISECONDS >= exp * 1000) {
          if (accessTokenRefreshPromise === null) {
            accessTokenRefreshPromise = refreshAccessToken(refreshToken);
          }

          const newToken = await accessTokenRefreshPromise;
          accessTokenRefreshPromise = null;

          if (newToken !== null) {
            headers.set('Authorization', `Bearer ${newToken}`);
          }
        }
      }

      const response = await fetch(requestUrl, {
        method,
        body,
        headers,
      });

      if (!response.ok) {
        if (response.status === 404) {
          return null;
        }

        throw new Error(response.statusText);
      }

      const contentType = response.headers.get('content-type');

      if (contentType && contentType.includes('application/json')) {
        return (await response.json()) as T;
      } else if (contentType && contentType.includes('text/plain')) {
        return (await response.text()) as unknown as T;
      } else {
        // TODO should we be parsing stuff that isn't JSON?
        return null;
        // return (await response.blob()) as unknown as T;
      }
    } catch (exception) {
      retryCount++;
      if (retryCount === retryLimit) {
        throw exception;
      }
    }
  }
  // Should never be hit
  return null;
}

const getRefreshAccessTokenData = async (refreshToken: string) => {
  let client_id, client_secret, scope;

  if (env.REACT_APP_SDK_MODE) {
    const { clientId, clientSecret } = await getSdkAuth();
    client_id = clientId;
    client_secret = clientSecret;
    scope = 'offline_access profile partners-user';
  } else {
    client_id = 'advocate';
    client_secret = 'secret';
    scope = 'offline_access profile advocate';
  }

  const data = {
    scope,
    client_id,
    client_secret,
    grant_type: 'refresh_token',
    refresh_token: refreshToken,
  };

  return data;
};

const refreshAccessToken = async (
  refreshToken: string
): Promise<string | null> => {
  const data = await getRefreshAccessTokenData(refreshToken);

  const response = await fetch(`${env.API_URL}/auth/connect/token`, {
    method: 'POST',
    body: toURLSearchParams(data),
  });

  if (response.ok) {
    const responseData = await response.json();
    await setUserTokens(responseData.access_token, responseData.refresh_token);
    return responseData.access_token;
  } else if (response.status === 400) {
    await setUserTokens('', '');
  }

  return null;
};

const toURLSearchParams = (data: Record<string, string | undefined | null>) => {
  const params = new URLSearchParams();

  Object.entries(data).forEach(([key, value]) => {
    if (value !== undefined && value !== null) {
      params.append(key, value);
    }
  });

  return params;
};

const sdkAuth = async (): Promise<AuthResponse | null> => {
  try {
    const { clientId, clientSecret, sharedKey } = await getSdkAuth();

    const { userId, userCurrency } = await getSdkUser();

    const timestamp = Math.floor(Date.now() / 1000).toString();
    const signatureData = `${userId}|${timestamp}|${sharedKey}`;
    const signature = await CryptoJS.SHA256(signatureData).toString();

    const data = {
      scope: 'profile offline_access partners-user',
      client_id: clientId,
      client_secret: clientSecret,
      grant_type: 'partners-user',
      unique_id: userId,
      timestamp: timestamp,
      signature: signature,
      currency: userCurrency,
    };

    const response = await fetch(`${env.API_URL}/auth/connect/token`, {
      method: 'POST',
      body: toURLSearchParams(data),
    });

    if (response.ok) {
      const responseData = await response.json();
      await setUserTokens(
        responseData.access_token,
        responseData.refresh_token
      );

      return {
        accessToken: responseData.access_token,
        refreshToken: responseData.refresh_token,
      };
    }

    return null;
  } catch (e) {
    console.error(e);
    return null;
  }
};

export const getOtl = async (): Promise<string | null> => {
  try {
    return await makeApiRequest<string>('/auth/partners/generate-otl');
  } catch (e) {
    console.error(e);
    return null;
  }
};
