interface CacheObject<T> {
  value: T;
  timeAdded: number;
}

type CacheContainer<T> = Record<string, CacheObject<T>>;

const now = () => Math.floor(new Date().getTime() / 1_000);

class SimpleCache<T> {
  ttl: number;
  name: string;

  constructor(name: string, ttl = 3_600) {
    this.ttl = ttl;
    this.name = name;
  }

  getAll() {
    return new Promise<CacheContainer<T>>((resolve) => {
      browser.storage.local.get(
        [this.name],
        (item: Record<string, unknown>) => {
          if (this.name in item) {
            const cache = item[this.name] as CacheContainer<T>;

            const expireBefore = now() - this.ttl;

            Object.entries(cache).forEach(([key, value]) => {
              if (value.timeAdded < expireBefore) {
                delete cache[key];
              }
            });

            resolve(cache);
          } else {
            resolve({});
          }
        }
      );
    });
  }

  async get(key: string) {
    const cache = await this.getAll();
    const cachedObject = cache[key];

    if (cachedObject === undefined) {
      return undefined;
    }

    return cachedObject.value;
  }

  async set(key: string, value: T) {
    const cache = await this.getAll();
    cache[key] = {
      value,
      timeAdded: now(),
    };
    await browser.storage.local.set({ [this.name]: cache });
  }

  async delete(key: string) {
    const cache = await this.getAll();
    if (cache[key] === undefined) {
      return;
    }
    delete cache[key];
    await browser.storage.local.set({ [this.name]: cache });
  }

  replace(cache: CacheContainer<T>) {
    browser.storage.local.set({ [this.name]: cache });
  }

  async append(update: Record<string, T>) {
    const cache = await this.getAll();

    const cacheUpdate = Object.keys(update).reduce(
      (a, key) => ({ ...a, [key]: { value: update[key], timeAdded: now() } }),
      { ...cache }
    );
    this.replace(cacheUpdate);
  }

  async appendWithSameValue(keys: string[], value: T) {
    const cache = await this.getAll();

    const cacheUpdate = keys.reduce(
      (a, key) => ({ ...a, [key]: { value, timeAdded: now() } }),
      { ...cache }
    );
    this.replace(cacheUpdate);
  }

  clear() {
    browser.storage.local.set({ [this.name]: {} });
  }
}

export default SimpleCache;
