const tabCheckoutNotificationsInteracted = new Map<number, Set<string>>();

export const recordCheckoutNotificationInteraction = (
  tabId: number,
  domain: string
) => {
  if (!tabCheckoutNotificationsInteracted.has(tabId)) {
    tabCheckoutNotificationsInteracted.set(tabId, new Set<string>());
  }

  const domains = tabCheckoutNotificationsInteracted.get(tabId);

  if (domains === undefined) {
    return;
  }

  domains.add(domain);
};

export const recordTabClosed = (tabId: number) => {
  if (tabCheckoutNotificationsInteracted.has(tabId)) {
    tabCheckoutNotificationsInteracted.delete(tabId);
  }
};

export const hasInteractedWithCheckoutNotification = (
  tabId: number,
  domain: string
) => {
  if (!tabCheckoutNotificationsInteracted.has(tabId)) {
    return false;
  }

  const domains = tabCheckoutNotificationsInteracted.get(tabId);

  if (domains === undefined) {
    return false;
  }

  return domains.has(domain);
};

const urlFragmentRegex = (fragment: string) => {
  return new RegExp(`\\/${fragment}([\\/\\?#\\.].*)?$`, 'i');
};

const domainRegex = (domain: string) => {
  return new RegExp(`^https:\\/\\/${domain}([\\/\\?#].*)?$`, 'i');
};

