import { DealSummaryWithDomains } from '../api/types';
import { DealOpenMessage, InternalMessage } from '../common/messageTypes';
import env from '../env';
import { getCurrentTab, getDomainFromUrl } from '../common/helpers';
import {
  getTrackingUrl,
  Interaction,
  setInteraction,
} from '../api/services/deals';
import { sendTabMessage } from './tab-messaging';
import { setAutomaticActivation } from '../common/preferences';
import { recordCheckoutNotificationInteraction } from './checkout-processor';
import { getAppScheme } from '../api/config';

export const initExtensionMessaging = () => {
  browser.runtime.onMessage.addListener(
    async (
      request: unknown,
      sender: browser.runtime.MessageSender,
      sendResponse: (response: object) => boolean
    ) => {
      const message = request as InternalMessage;

      if (message) {
        switch (message.type) {
          case 'openDeal':
            {
              const tab = sender?.tab ?? (await getCurrentTab());
              const result = await openDealInformingUser(message, tab);

              sendResponse?.(message);
            }
            break;
          case 'openApp':
            {
              const appScheme = await getAppScheme();

              if (appScheme.scheme) {
                browser.tabs.create({
                  url: `${appScheme.scheme}://`,
                });
              }
            }
            break;
          case 'activateAllDeals':
            {
              await setAutomaticActivation(true);
            }
            break;
          case 'closeCheckoutPopup': {
            if (sender && sender.tab && sender.tab.id && sender.url) {
              const domain = getDomainFromUrl(sender.url);

              if (domain) {
                recordCheckoutNotificationInteraction(sender.tab.id, domain);
              }
            }
            break;
          }
        }
      }
    }
  );
};

export const openDealInformingUser = async (
  request: DealOpenMessage,
  tab: browser.tabs.Tab,
  interaction: Interaction = { type: 'activate' }
) => {
  try {
    sendTabMessage(tab.id, {
      type: 'dealActivating',
      deal: request.deal,
    });

    const newTab = await openDeal(
      request.deal,
      tab,
      request.sameTab,
      interaction
    );

    if (newTab || tab) {
      sendTabMessage((newTab ?? tab).id, {
        type: 'dealActivated',
        deal: request.deal,
      });
    }
  } catch (e) {
    // TODO handle when we can't open the deal

    if (request.sameTab && tab) {
      if (tab.url) {
        const domain = getDomainFromUrl(tab.url);
        await setInteraction(domain, { type: 'error' });
      }

      sendTabMessage(tab.id, {
        type: 'dealActivationFailed',
        deal: request.deal,
      });
    }

    return false;
  }
};

let tabsBeingTracked: number[] = [];

const openDeal = async (
  deal: DealSummaryWithDomains,
  currentTab: browser.tabs.Tab | undefined,
  sameTab: boolean,
  interaction: Interaction
): Promise<browser.tabs.Tab | null> => {
  const domain = getDomainFromUrl(currentTab?.url ?? '');
  const expectedDomains = sameTab ? [domain] : deal.domains;

  if (domain) {
    await setInteraction(domain, interaction);
  }

  const trackingUrl = await getTrackingUrl(
    deal,
    {
      s: `ios_extension_deal_activated`,
    },
    sameTab ? currentTab?.url : undefined
  );

  if (trackingUrl === null) {
    throw new Error('Deal activation failed');
  }

  if (sameTab && currentTab?.id && browser.tabs.update) {
    await browser.tabs.update(currentTab.id, { active: true });
  }

  const tab = await browser.tabs.create({
    active: !sameTab,
    url: trackingUrl,
    pinned: sameTab,
  });

  let timeout: NodeJS.Timeout | undefined = undefined;
  let tabUrl = tab.url;
  return new Promise((resolve, reject) => {
    const removeTab = async () => {
      if (sameTab && tab.id !== undefined) {
        await browser.tabs.remove(tab.id);
      }

      if (tab.id) {
        tabsBeingTracked = tabsBeingTracked.filter((x) => x !== tab.id);
      }
    };

    const handleTabUpdated = (
      tabId: number,
      info: {
        audible?: boolean;
        discarded?: boolean;
        favIconUrl?: string;
        mutedInfo?: browser.tabs.MutedInfo;
        pinned?: boolean;
        status?: string;
        title?: string;
        url?: string;
      },
      updatedTab: browser.tabs.Tab
    ) => {
      // We are only interested in changes to the tab we just opened
      if (tabId !== tab.id) {
        return;
      }

      tabUrl = updatedTab.url;

      // We only need to react to url and status changes
      if (info.url === undefined && info.status === undefined) {
        return;
      }

      // If the url isn't set, ignore
      if (updatedTab.url === undefined) {
        return;
      }

      const updatedRedirectedDomain = getDomainFromUrl(updatedTab.url);

      // If it's not yet reached the website again, ignore
      if (
        updatedRedirectedDomain === null ||
        (expectedDomains &&
          !expectedDomains.some((d) => updatedRedirectedDomain === d))
      ) {
        return;
      }

      if (info.url !== undefined) {
        // TODO Check for and record the tracking parameter if possible for deactivation detection
        // handleUrlTrackingParameters(updatedRedirectedDomain, updatedTab.url);
      }

      // If the tab hasn't finished loading, ignore
      if (updatedTab.status !== 'complete') {
        return;
      }

      // TODO Used for tracking that a deal from the K window has been activated
      if (!sameTab && interaction) {
        setInteraction(updatedRedirectedDomain, interaction);
      }

      // We've now reached the site and it's loaded so remove the tab after 2 seconds

      setTimeout(async () => {
        await removeTab();
        resolve(!sameTab ? tab : null);
      }, 2_000);

      browser.tabs.onUpdated.removeListener(handleTabUpdated);

      if (timeout !== undefined) {
        clearTimeout(timeout);
      }
    };

    browser.tabs.onUpdated.addListener(handleTabUpdated);

    if (tab.id) {
      tabsBeingTracked.push(tab.id);
    }

    // If we haven't detected a correct redirect pattern after 20 seconds, just close the new tab
    timeout = setTimeout(() => {
      removeTab();

      browser.tabs.onUpdated.removeListener(handleTabUpdated);
      reject(
        `Deal activation timed out. Expected domains: ${
          expectedDomains && expectedDomains.join(', ')
        }. Tab url: ${tabUrl}`
      );
    }, 20_000);
  });
};
