export const getDomainFromUrl = (url: string) => {
  const matches = /^(?:https?:\/\/)?(?:www\.)?([^:/\n?]+)/gim.exec(url);

  if (!(matches && matches[1])) {
    return null;
  }

  return matches[1];
};

export const getTabsWithMatchingDomain = async (domain: string) => {
  return await browser.tabs.query({
    url: [`*://${domain}/*`, `*://www.${domain}/*`],
  });
};

export const setCount = (tabId: number, count: number) => {
  browser.browserAction.setBadgeText({
    tabId,
    text: count > 0 ? count.toString() : '',
  });
};

export const getCurrentTab = () => {
  return new Promise<browser.tabs.Tab>((resolve) => {
    browser.tabs.query({ active: true, currentWindow: true }).then((result) => {
      resolve(result[0]);
    });
  });
};
