import { AuthConfigMessage } from '../common/messageTypes/nativeMessage';

export const initConfigurationProcessor = () => {
  setupAuth();
};

const setupAuth = () => {
  browser.runtime
    .sendNativeMessage('application.id', {
      type: 'authConfig',
    })
    .then((response) => {
      const authConfigResponse = response as AuthConfigMessage;
      console.log('Received sendNativeMessage response:', response);
      if (authConfigResponse) {
        console.log('authConfig');
      }
    })
    .catch(console.log);
};
