import { DealSummaryWithDomains } from '../api/types';
import {
  getDomainFromUrl,
  getTabsWithMatchingDomain,
  setCount,
} from '../common/helpers';
import {
  getInteractionByDomain,
  lookupByDomain,
  setInteraction,
} from '../api/services/deals';
import { hasAutomaticActivation } from '../common/preferences';
import { sendTabMessage } from './tab-messaging';
import { openDealInformingUser } from './extension-messaging';
import { DealOpenMessage } from '../common/messageTypes';
import {
  hasInteractedWithCheckoutNotification,
  isCheckoutPage,
  recordTabClosed,
} from './checkout-processor';
import SimpleCache from '../common/simpleCache';
import { affiliateLinks } from './affiliate-links';
import env from '../env';

const IS_REDIRECT_TAB = 1; // Constant to indicate a redirect tab

const redirectTabsCache = new SimpleCache<number | null>(
  'RedirectTabs',
  30 // 30seconds
);

const tabsRedirectedFromClicks = new Set<number>();

export const initDealProcessor = () => {
  handleTabUpdates();
  handleTabClosed();
};

const handleTabClosed = () => {
  browser.tabs.onRemoved.addListener((id) => {
    recordTabClosed(id);
  });
};

const handleTabUpdates = () => {
  browser.tabs.onUpdated.addListener(async (_, { status }, tab) => {
    if (
      status === 'loading' ||
      (status === undefined && tab.status === 'loading') // this captures tabs that redirecting, still loading during transition period
    ) {
      // do we want to stand down on affiliate networks redirect?
      if (env.REACT_APP_STANDDOWN) {
        await handleRedirectFromClicks(tab);
      }
    } else if (status === 'complete') {
      await onTabUpdated(tab);
      await redirectTabsCache.delete(tab.id?.toString() ?? '');
    }
  });
};

const onTabUpdated = async (tab: browser.tabs.Tab) => {
  if (tab.url === undefined || tab.id === undefined) {
    return;
  }

  const { url, id } = tab;

  try {
    const domain = getDomainFromUrl(url);

    if (domain === null) {
      return;
    }
    const isCheckout = isCheckoutPage(url);
    const checkoutInteraction = hasInteractedWithCheckoutNotification(
      id,
      domain
    );

    const interaction = await getInteractionByDomain(domain);

    // find a deal
    const deal = await lookupByDomain(domain);

    if (interaction) {
      if (isCheckout && !checkoutInteraction && deal) {
        sendTabMessage(id, {
          type: 'dealCheckoutPage',
          deal,
        });
      }

      return;
    }

    const matchingDomainTabs = await getTabsWithMatchingDomain(domain);
    if (deal !== null) {
      console.log('Got a matching deal for ', domain, '. ', deal);

      const discountCodesCount =
        deal.codes?.filter((c) => Boolean(c.code)).length ?? 0;
      setCount(id, discountCodesCount > 0 ? discountCodesCount : 1);

      await handleDealsAvailable(tab, deal, domain);
    } else {
      matchingDomainTabs?.forEach((t) => {
        if (t.id) {
          setCount(t.id, 0);
        }
      });
      console.log('No deals matching domain ', domain);
    }
  } catch (e) {
    console.error(e);
  }

  tabsRedirectedFromClicks.delete(id);
};

const handleDealsAvailable = async (
  tab: browser.tabs.Tab,
  deal: DealSummaryWithDomains,
  domain: string
) => {
  if (await hasAutomaticActivation()) {
    await handleAutomaticDealActivation(domain, deal, tab);
  } else {
    sendTabMessage(tab.id, {
      type: 'dealsAvailable',
      deal,
    });
  }
};

const handleAutomaticDealActivation = async (
  domain: string | null,
  deal: DealSummaryWithDomains,
  tab: browser.tabs.Tab
) => {
  sendTabMessage(tab.id, {
    type: 'dealActivating',
    deal: deal,
  });

  const interaction = await getInteractionByDomain(domain);

  if (interaction === null) {
    const request: DealOpenMessage = {
      type: 'openDeal',
      deal,
      tabId: tab.id,
      sameTab: true,
    };

    await openDealInformingUser(request, tab, { type: 'automatic' });
  }
};

const handleRedirectFromClicks = async (tab: browser.tabs.Tab) => {
  if (tab.url && tab.id) {
    if (affiliateLinks.some((c) => tab.url?.includes(c))) {
      redirectTabsCache.set(tab.id.toString(), IS_REDIRECT_TAB);
    } else {
      const domain = getDomainFromUrl(tab.url);

      if (!(await cameFromKindred(tab.id)) || domain === null) {
        return;
      }

      const interaction = await getInteractionByDomain(domain);
      if (interaction === null) {
        await setInteraction(domain, { type: 'link' });
      }
      return;
    }
  }
};

const cameFromKindred = async (tabId: number) => {
  const redirect = await redirectTabsCache.get(tabId.toString());
  return redirect === IS_REDIRECT_TAB;
};
