import {
  AppSchemeMessage,
  AuthConfigMessage,
  SDKAuthConfigMessage,
  SDKUserMessage,
} from '../common/messageTypes/nativeMessage';

export const getUserTokens = (): Promise<AuthConfigMessage> => {
  return getInternalMessage<AuthConfigMessage>('authConfig');
};

export const setUserTokens = (accessToken: string, refreshToken: string) => {
  return browser.runtime.sendNativeMessage('application.id', {
    type: 'setAuthConfig',
    accessToken,
    refreshToken,
  });
};

export const getSdkAuth = (): Promise<SDKAuthConfigMessage> => {
  return getInternalMessage<SDKAuthConfigMessage>('sdkAuthConfig');
};

export const getSdkUser = (): Promise<SDKUserMessage> => {
  return getInternalMessage<SDKUserMessage>('sdkUserConfig');
};

export const getAppScheme = (): Promise<AppSchemeMessage> => {
  return getInternalMessage<AppSchemeMessage>('getSchemeConfig');
};

const getInternalMessage = <T>(type: string): Promise<T> => {
  return new Promise((resolve, reject) => {
    browser.runtime
      .sendNativeMessage('application.id', {
        type,
      })
      .then((response) => {
        if (response) {
          const authConfigResponse = response as unknown as T;
          console.log('Received sendNativeMessage response:', response);
          if (authConfigResponse) {
            resolve(authConfigResponse);
          }
        }
      })
      .catch(reject);
  });
};
